.clients {
  padding: 20px;
  background-color: $color-gray-100;
}

.clients__container {
  @media (min-width: $breakpoint-large) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;
  }
}

.clients__list {
  text-align: center;
}

.clients__client-desk {
  color: $color-gray-700;
}

.clients__title {
  color: $color-gray-700;
  font-weight: 400;
  font-size: 12px;
  line-height: 1;
}

.clients__definition {
  font-family: $font-heading;
  font-weight: 400;
  font-size: 24px;
  line-height: 1;
  color: $color-black;
}

.clients__cost {
  font-family: $font-heading;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: $color-black;
}
