.page-header__nav {
  @media (min-width: $breakpoint-medium) {
      display: grid;
      grid-template-columns: 174px 1fr;
      gap: 16px;
      width: 708px;
      // width: 93%;
      // padding-left: 30px;
      // padding-right: 30px;
      margin: 0 auto;
      padding: 24px 0;
    }

    @media (min-width: $breakpoint-large) {
      width: 1220px;
      // width: 94%;
      margin: 0 auto;
    }
}


.page-header__nav--main {
  @media (min-width: $breakpoint-large) {
    grid-template-columns: 174px 50%;
    justify-content: space-between;
  }
}

.page-header__bar {
  @media (max-width: $breakpoint-small-down) {
    // width: 320px;
    margin: 0 auto;
    padding: 10px 20px 18px;
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    gap: 16px;
    align-items: center;
  }
}

.page-header__logo {
  grid-column: 1/3;
}

.page-header__main-nav-toggler {
  display: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url("../img/icons/toggler-burger.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  border: none;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  &:focus {
    opacity: 0.6;
    outline: 2px solid $color-black;
  }

  @media (max-width: $breakpoint-small-down) {
    .page-header__main-nav--closed &{
      display: block;
    }
  }

  @media (max-width: $breakpoint-small-down) {
    .page-header__main-nav--opened &{
      display: block;
      background-image: url("..//img/icons/toggler-closed.svg");
    }
  }

  @media (min-width: $breakpoint-medium) {
    display: none;
  }
}

  .page-header__menu {
    margin: 0;
    padding: 0;

    @media (max-width: $breakpoint-small-down) {
      .page-header__main-nav--closed & {
        display: none;
      }
    }

    @media (max-width: $breakpoint-small-down) {
      .page-header__main-nav--opened & {
        display: block;
        position: absolute;
        z-index: 2;
        left: 0;
        right: 0;
      }
    }
}


